import React from 'react'
import FieldErrorComponent from 'shared/inputs/FieldErrorComponent'
import styles from 'shared/inputs/input-component'

function InputComponent({
  field,
  form: {touched, errors},
  ...props
}) {
  const {name} = field
  return (
    <div>
      <input type='text' className={styles.inputField} {...field} {...props} />
      {touched[name] && errors[name] && <FieldErrorComponent message={errors[name]} />}
    </div>
  )
}

export default InputComponent