
module.exports = requestModule => {
  
  return ({headers, errorProcessor} = {}) => {
    if (errorProcessor && typeof errorProcessor !== 'function') {
      throw new Error('Provided error processor must be a function')
    }
  
    function request(method) {
      return (url, payload) => {
        return requestModule(url, {method, headers, body: payload})
          .then(response => {
            const {status, body} = response
            if (status < 200 || status > 300) {
              return Promise.reject({status, errors: body.errors || body})
            }
            return body
          })
          .catch(error => {
            if (!errorProcessor) throw new Error(error)
            return errorProcessor(error)
          })
      }
    }
  
    return {
      post: request('POST'),
      put: request('PUT'),
      patch: request('PATCH'),
      get: request('GET'),
      delete: request('DELETE')
    }
  }
  
}
