import React from 'react'
import {Formik, Field} from 'formik'
import * as yup from 'yup'
import UserSignUpService from 'modules/users/userSignUp/UserSignUpService'
import InputComponent from 'shared/inputs/InputComponent'

function UserSignUpFormContainer() {

  const initialValues = {email: '', password: ''}

  const validationSchema = yup.object().shape({
    email: yup.string().email().required(),
    password: yup.string().required().test(
      'is-password',
      'Password must be 8+ characters, and include character types such as- "Password123!"',
      password => /^(?=.*[0-9])(?=.*[a-z])(?=.*[A-Z])(?=.*[!@#$%^&*]).{8,}$/.test(password)
    )
  })

  const handleFormSubmit = userSignUp => {
    return (values, {setSubmitting}) => {
      userSignUp(values)
        .then(user => {
          setSubmitting(false)
        }, error => {
          setSubmitting(false)
        })
    }
  }

  return (
    <div>
      <UserSignUpService render={({userSignUp}) => (
        <Formik
          initialValues={initialValues}
          validationSchema={validationSchema}
          onSubmit={handleFormSubmit(userSignUp)}
          render={({
            values,
            handleSubmit,
            isSubmitting,
            isValid
          }) => {
            return (
              <form onSubmit={handleSubmit}>
                <Field type='email' name='email' component={InputComponent} />
                <Field type='password' name='password' component={InputComponent} />
                <button type='submit' disabled={isSubmitting || !isValid}>
                  Submit
                </button>
              </form>
            )
          }} />
      )} />
    </div>
  )
}

export default UserSignUpFormContainer